  font-size: 20px;
  font-family: "Lato", sans-serif;
  color: #fff;
} */
.footer-container {
  margin-top: calc(5% + 30px);
  text-align: center;
  position: relative;
  margin-bottom: 30px;
}
.connect-footer{
  color: #374252;
  font-family: Rubik,sans-serif;
  margin-bottom: 30px;
}
